@charset "UTF-8";
.nav__list-logo {
  color: rgba(51, 39, 0, 0.8509803922);
  font-size: 1.5rem;
}

@font-face {
  font-family: "helveticaneue";
  src: url(/app/helveticaneue/HelveticaNeueBd.ttf) format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "helveticaneue";
  src: url(/app/helveticaneue/HelveticaNeueMed.ttf) format("truetype");
  font-weight: 500;
}
@font-face {
  font-family: "helveticaneue";
  src: url(/app/helveticaneue/HelveticaNeueIt.ttf) format("truetype");
  font-style: italic;
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: helveticaneue;
  padding: 0;
  margin: 0;
  background-color: rgb(255, 251, 240);
  color: rgba(51, 39, 0, 0.8980392157);
  overflow-x: hidden;
}

img {
  width: 100%;
  height: 100%;
}

.btn {
  background-color: #ffcc54;
  border: none;
  font-weight: 400;
  cursor: pointer;
}

.btn__sm {
  box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
  border-radius: 0.5rem;
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
}

.btn__md {
  box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
  border-radius: 0.9rem;
  padding: 0.7rem 2rem;
  font-size: 2rem;
  margin-top: 2rem;
}

.download {
  cursor: pointer;
}

.download-btn {
  display: inline-block;
  width: 19rem;
}

.search-btn {
  display: inline-block;
  background-color: rgba(120, 129, 128, 0.7490196078);
  width: 5rem;
  padding: 2em;
  border-radius: 0 1rem 1rem 0;
}

.btn:hover,
.btn:active {
  background-color: rgba(255, 204, 84, 0.8);
  transition: all 2s ease-in-out;
}

.btn:active {
  transform: translateY(50%);
}

@media (min-width: 980px) {
  .btn__md {
    box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
    border-radius: 0.9rem;
    padding: 1.5rem 3.5rem;
    font-size: 3.4rem;
    margin-top: 2rem;
  }
  .btn__sm {
    box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
    border-radius: 0.9rem;
    padding: 1.2rem 2rem;
    font-size: 1.8rem;
  }
  .search-btn img {
    margin-left: 1rem;
  }
}
.nav {
  position: absolute;
  text-align: center;
  top: 100%;
  right: 0;
  background-color: rgba(255, 251, 240, 0.9);
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 400ms ease-in-out;
}
.nav__logo {
  text-align: left;
  margin-left: 1em;
}
.nav__lists {
  list-style-type: none;
}
.nav__list {
  margin-bottom: 1em;
  margin-right: 1em;
}
.nav__list__link {
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: rgba(138, 106, 0, 0.8980392157);
}
.nav__toggle:checked ~ .nav {
  transform: scale(1, 1);
}
.nav__toggle:checked ~ .nav__list__link {
  opacity: 1;
}
.nav__toggle {
  display: none;
}
.nav__toggle__label {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 1em;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}

@media (min-width: 980px) {
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
  }
  .nav__logo {
    margin-right: auto;
  }
  .nav {
    all: unset;
    font-size: 1.8rem;
    font-weight: 500;
  }
  .nav__lists {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav__toggle__label {
    display: none;
  }
  .nav__list--highlight {
    text-decoration: underline solid #8a6a00;
    text-underline-offset: 0.7rem;
  }
}
.footer {
  color: #fffbf0;
  background-color: rgba(132, 139, 137, 0.7490196078);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.footer__icon {
  display: inline-block;
  width: 40%;
  margin-right: 5rem;
}
.footer__details {
  flex-basis: 80%;
}
.footer__details p {
  padding-left: 0;
  padding-top: 0;
}
.footer__contact ul {
  padding: 1rem;
  margin-right: 2rem;
}
.footer__contact li {
  list-style-type: none;
  flex-basis: 33%;
