}
.footer__contact__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: 80%;
  margin-right: 8rem;
  margin: auto;
}

.hr {
  flex-basis: 100%;
  color: rgba(255, 251, 240, 0.5019607843);
  margin-bottom: 0;
}

@media (max-width: 411px) {
  .footer__contact__container {
    font-size: 0.9rem;
    margin: 0;
    gap: 0;
    padding: 0;
    flex-basis: 100%;
  }
}
@media (min-width: 980px) {
  .footer {
    font-size: 1.8rem;
    padding: 0rem;
    gap: 0rem;
  }
  .footer h2 {
    font-size: 3rem;
  }
  .footer p {
    width: 80%;
  }
  .footer__details {
    flex-basis: 40%;
    padding: 0;
  }
  .footer__contact__container {
    font-size: 2rem;
    display: flex;
    flex-basis: 49%;
    padding: 0;
    margin: 0;
  }
  .hr {
    flex-basis: 90%;
    color: rgba(255, 251, 240, 0.5019607843);
    margin-bottom: 0;
  }
  .last-p {
    padding: 0;
    margin-top: 0;
    margin-right: 12rem;
    width: 100%;
  }
}
