@use './typography.scss' as  *;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: helveticaneue;
  padding: 0;
  margin: 0;
  background-color: rgba(255, 251, 240, 1);
  color: #332700e5;
  overflow-x: hidden;
}
img {
  width: 100%;
  height: 100%;
}
